import React, { Component } from 'react';
import './app.scss';
import './rwttabs.scss';
import { Content } from 'carbon-components-react/lib/components/UIShell';
import IAGHeader from './components/IAGHeader';
import { Route, Switch } from 'react-router-dom';
import landingPage from './content/landingPage';
import versionPage from './content/versionPage'
import secretsPage from './content/secretsPage'
import serverPage from './content/serverPage'
import identityPage from './content/identityPage'
import resource_serversPage from './content/resource_serversPage'
import policiesPage from './content/policiesPage'
import authorizationPage from './content/authorizationPage'
import servicesPage from './content/servicesPage'
import loggingPage from './content/loggingPage'
import advancedPage from './content/advancedPage'




class App extends Component {
  render() {
    return (
      <>
        <IAGHeader />
        <Content>
          <div className="bx--row">
            <section className="bx--offset-lg-3 bx--col-lg-13">
              <Switch>
                <Route exact path="/" component={landingPage} />
    <Route path="/version" component= {versionPage} />
            <Route path="/secrets" component= {secretsPage} />
            <Route path="/server" component= {serverPage} />
            <Route path="/identity" component= {identityPage} />
            <Route path="/resource_servers" component= {resource_serversPage} />
            <Route path="/policies" component= {policiesPage} />
            <Route path="/authorization" component= {authorizationPage} />
            <Route path="/services" component= {servicesPage} />
            <Route path="/logging" component= {loggingPage} />
            <Route path="/advanced" component= {advancedPage} />

              </Switch>
            </section>
          </div>
        </Content>
      </>
    );
  }
}

export default App;
















































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































