const handleChange = () =>{
    var yamlText = window.iagyaml;

    var json = {};

    if(yamlText !== null && yamlText !== undefined) {
        json = safeLoad(yamlText);
        if(json === undefined || json === null) {
          json = {};
        }
    }

    var newElem = {};
    var newkerberos = {};
    if(document.getElementById("services_kerberos_keytab_id").value.endsWith(":") !== true) {
    newkerberos["keytab"] = document.getElementById("services_kerberos_keytab_id").value;
    }
    if(document.getElementById("services_kerberos_keytab_spn_id").value.endsWith(":") !== true) {
    newkerberos["keytab_spn"] = document.getElementById("services_kerberos_keytab_spn_id").value;
    }
    var newlibdefaults = {};
    newlibdefaults["properties"] = services_kerberos_libdefaults_propertiesrowData.services_kerberos_libdefaults_properties;
    newkerberos["libdefaults"] = newlibdefaults;
    newkerberos["realms"] = services_kerberos_realmsrowData.services_kerberos_realms;
    newkerberos["capaths"] = services_kerberos_capathsrowData.services_kerberos_capaths;
    newkerberos["properties"] = services_kerberos_libdefaults_propertiesrowData.services_kerberos_libdefaults_properties;
    newElem["kerberos"] = newkerberos;
    var newredis = {};
    if(document.getElementById("services_redis_key_prefix_id").value.endsWith(":") !== true) {
    newredis["key_prefix"] = document.getElementById("services_redis_key_prefix_id").value;
    }
    if(document.getElementById("services_redis_default_collection_id").value.endsWith(":") !== true) {
    newredis["default_collection"] = document.getElementById("services_redis_default_collection_id").value;
    }
    newredis["collections"] = services_redis_collectionsrowData.services_redis_collections;
    newElem["redis"] = newredis;
    newElem["credential"] = services_credentialrowData.services_credential;
    newElem["realms"] = services_kerberos_realmsrowData.services_kerberos_realms;
    newElem["collections"] = services_redis_collectionsrowData.services_redis_collections;
    newElem["capaths"] = services_kerberos_capathsrowData.services_kerberos_capaths;
    newElem["properties"] = services_kerberos_libdefaults_propertiesrowData.services_kerberos_libdefaults_properties;
    json["services"] = newElem;

    var yamlop = jsonToYaml(json);
    window.iagyaml = yamlop;
};

