export function getComments(key) {

    var retVal = "";

    var comments = window["iag.comments"];

    if(comments) {

        var keyParts = key.split(".");

        var obj = comments;
        for(var i = 0; i < keyParts.length; i++) {

            if(keyParts[i].endsWith("]") === true) {
                var partA = keyParts[i].slice(0, keyParts[i].lastIndexOf("["));
                var partB = keyParts[i].slice(keyParts[i].lastIndexOf("[") + 1, keyParts[i].length - 1);

                var arrayIndex = parseInt(partB);
                if(obj && obj[partA] && obj[partA]["items"] && obj[partA]["items"].length > arrayIndex) {
                    obj = obj[partA]["items"][arrayIndex];
                }
            } else {
                obj = obj[keyParts[i]];
            }

            if(obj === undefined || obj === null) {
                break;
            }
        }

        if(obj) {

            if(obj && obj["comments"]) {
                for(var i = 0; i < obj["comments"].length; i++) {
                    retVal += obj["comments"][i];
                }
            }
        }
    }

    return retVal;
};

export function safeLoad(input) {
    var retVal;
    try {
        retVal = yaml.load(input);
        var result = loadComments(input, retVal);
        window["iag.comments"] = result[0];
        window["iag.order"] = result[1];
    } catch (err) {
        // If there is a parsing error, check to see if its a special character
        // that needs to be put in quotes.
        var lines = input.split("\n");
        var newInput = "";
        for(var i = 0; i < lines.length; i++) {
            var parts = lines[i].split(":", 2);
            newInput += parts[0];
            if(parts.length > 1) {
                var handled = false;
                if(typeof input === "string") {
                    for(var i2 = 0; i2 < yamlSpecialChars.length; i2++) {
                        if(parts[1].includes(yamlSpecialChars[i2])) {
                            newInput += ": \"" + parts[1].trim() + "\"";
                            handled = true;
                            break;
                        }
                    }
                }
                if(handled === false) {
                    newInput += ": " + parts[1].trim();
                }
            }
            newInput += "\n";
        }
        try {
            // Try again
            retVal = yaml.load(newInput);
            console.log("Quoted invalid YAML characters.")
        } catch(err) {
            console.error(err);
        }
    }
    return retVal;
}

export const yamlSpecialChars = ['{', '}', '[', ']', ',', '&', ':', '*', '#', '?', '|', '-', '<', '>', '=', '!', '%', '@', '\\'];

export function escapeYaml(input, indent) {
    if(typeof input === "string") {
        if(input.indexOf("\n") > -1) {
            if(indent === undefined || indent === null) {
                indent = "";
            }
            indent = indent + "  ";
            input = input.replace(/\n/g, "\n" + indent);
            input = "|\n" + indent + input;
            return input;
        } else {
            for(var i = 0; i < yamlSpecialChars.length; i++) {
                if(input.includes(yamlSpecialChars[i])) {
                    return "\"" + input.replace(/\"/g, "\\\"") + "\"";
                }
            }
        }
    }
    return input;
};

export function getDivHeight(divId) {
    var mainDiv = document.getElementById(divId);
    if(mainDiv !== undefined && mainDiv !== null) {
      var hide = false;
      if (window.getComputedStyle(mainDiv).display === "none") {
        mainDiv.style.display = "block";
        hide = true;
      }
      var oh = mainDiv.offsetHeight;
      if(hide === true) {
        mainDiv.style.display = "none";
      }
      var mainHeight = (oh - 86);
      return mainHeight;
    }
};
// Toggle the state of a page description
export function toggleDescription(descId, snippetId, buttonId) {
    var btn = document.getElementById(buttonId);
    var desc = document.getElementById(descId);
    var snippet = document.getElementById(snippetId);
    if (desc.style.display === "none") {
      desc.style.display = "block";
      snippet.style.display = "none";
      btn.innerHTML = "Hide Full Description";
    } else {
      desc.style.display = "none";
      btn.innerHTML = "Show Full Description";
      snippet.style.display = "block";
    }
}

// Update style of all instances of the class
export function updateStyle(className, prop, value) {
    var x = document.getElementsByClassName(className);
    var i;
    for (i = 0; i < x.length; i++) {
      x[i].style[prop] = value;
    }
}

export function validateField(dataArray, setValid, setInvalidMessage, component, field, newValue){
    setValid(true);
    for(var i = 0; i < dataArray.length; i++) {
        if(dataArray[i].id == newValue) {
            setValid(false);
            setInvalidMessage("An element with the specified " + field + " already exists in the " + component + " array.");
        }
    }
}

// Update style of the first instance of the class found in a given div
export function updateStyle2(divId, className, prop, value) {
    if(divId) {
        var div = document.getElementById(divId);
        var x = div.getElementsByClassName(className);
        if(x.length > 0) {
            x[0].style[prop] = value;
        }
    }
}

export function jsonToYaml(json) {

    var retVal = "";

    var compOrder = window["iag.order"];

    var doneversion = false;
    var donesecrets = false;
    var doneserver = false;
    var doneidentity = false;
    var doneresource_servers = false;
    var donepolicies = false;
    var doneauthorization = false;
    var doneservices = false;
    var donelogging = false;
    var doneadvanced = false;

    if(compOrder) {
        for(var i = 0; i < compOrder.length; i++) {
            switch(compOrder[i]) {
                case "version":
                    retVal += versionJsonToYaml(json);
                    doneversion = true;
                    break;
                case "secrets":
                    retVal += secretsJsonToYaml(json);
                    donesecrets = true;
                    break;
                case "server":
                    retVal += serverJsonToYaml(json);
                    doneserver = true;
                    break;
                case "identity":
                    retVal += identityJsonToYaml(json);
                    doneidentity = true;
                    break;
                case "resource_servers":
                    retVal += resource_serversJsonToYaml(json);
                    doneresource_servers = true;
                    break;
                case "policies":
                    retVal += policiesJsonToYaml(json);
                    donepolicies = true;
                    break;
                case "authorization":
                    retVal += authorizationJsonToYaml(json);
                    doneauthorization = true;
                    break;
                case "services":
                    retVal += servicesJsonToYaml(json);
                    doneservices = true;
                    break;
                case "logging":
                    retVal += loggingJsonToYaml(json);
                    donelogging = true;
                    break;
                case "advanced":
                    retVal += advancedJsonToYaml(json);
                    doneadvanced = true;
                    break;
            }
        }
    }

    if(doneversion === false) retVal += versionJsonToYaml(json);
    if(donesecrets === false) retVal += secretsJsonToYaml(json);
    if(doneserver === false) retVal += serverJsonToYaml(json);
    if(doneidentity === false) retVal += identityJsonToYaml(json);
    if(doneresource_servers === false) retVal += resource_serversJsonToYaml(json);
    if(donepolicies === false) retVal += policiesJsonToYaml(json);
    if(doneauthorization === false) retVal += authorizationJsonToYaml(json);
    if(doneservices === false) retVal += servicesJsonToYaml(json);
    if(donelogging === false) retVal += loggingJsonToYaml(json);
    if(doneadvanced === false) retVal += advancedJsonToYaml(json);

    updateLinks(json);

    return retVal;
}
export function versionJsonToYaml(json) {

    var retVal = "";

    if(json.version == undefined || json.version == null) {
        json.version = "20.12";
    }

    getComments("version");
    retVal += "version: " + escapeYaml(json.version, "") + "\n";

    return retVal;
}

