          <ReactTooltip id={"server_protocols_tooltip_id" + idPrefix} place="top" effect="solid" class="tooltipScroll" delayHide={500}><div style={{ maxWidth: '50vw', maxHeight: 300, overflowY: 'scroll' }}>Specifies the protocols which will be supported by the server. The server will listen on a different port for each protocol: `http/8080` and `https/8443`. If no protocols are specified, the server will listen for https requests only.<br/><br/>Additionally, the server can be configured to expect the PROXY Protocol header by using the `*_proxy` values.<br/><br/>Note that if the proxy protocol is enabled for a given protocol, all traffic for that protocol will be expected to contain the PROXY protocol headers. That is, the following entries are exclusive:<br/> - `http` and `http_proxy`<br/> - `https` and `https_proxy`<br/><br/><table style={{textAlign:'left', border:'1px solid black'}}><tbody><tr style={{textAlign:'left', border:'1px solid black'}}><th style={{borderRight:'1px solid black', padding: '10px'}}>protocol    </th><th style={{borderRight:'1px solid black', padding: '10px'}}> Port </th><th style={{borderRight:'1px solid black', padding: '10px'}}> Description</th></tr><tr><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}>http        </td><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}> 8080 </td><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}> HTTP</td></tr><tr><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}>https       </td><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}> 8443 </td><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}> HTTPS</td></tr><tr><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}>http_proxy  </td><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}> 8080 </td><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}> HTTP with Proxy Protocol</td></tr><tr><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}>https_proxy </td><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}> 8443 </td><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}> HTTPS with Proxy Protocol</td></tr></tbody></table><br/><br/>Example:<br/><br/>server:<br/>  protocols:<br/>    - https<br/>    - http_proxy<br/></div></ReactTooltip>

