          <ReactTooltip id={"server_failover_domain_cookie_tooltip_id" + idPrefix} place="top" effect="solid" ><div style={{ maxWidth: 300 }}>A boolean flag indicating whether or not the failover cookie should include a domain. By including a domain the failover cookie will be sent to any server within the same DNS domain. For example, a failover cookie which is generated by `server_a.ibm.com` will also be sent to `server_b.ibm.com`. The name of the domain which is used in the failover cookie is derived from the Host header in the HTTP request.<br/></div></ReactTooltip>
