          <ReactTooltip id={"server_public_assets_content_tooltip_id" + idPrefix} place="top" effect="solid" ><div style={{ maxWidth: 300 }}>The content which will be used for the public assets.  The content can either be the name of a directory, relative to the `/var/iag/config` directory of the container, or a zip file.<br/></div></ReactTooltip>
