          <ReactTooltip id={"server_http2_tooltip_id" + idPrefix} place="top" effect="solid" ><div style={{ maxWidth: 300 }}>A boolean which indicates whether or not to enable support for HTTP/2 clients. HTTP/2 supports a reduced set of cipher suites. The minimum cipher is `TLS_ECDHE_RSA_WITH_AES_128_GCM_SHA256` which should be added to the `server/ssl/ciphers[]` entry.<br/><br/>Example:<br/>server:<br/>  http2: true<br/></div></ReactTooltip>
