          <ReactTooltip id={"server_content_security_policy_tooltip_id" + idPrefix} place="top" effect="solid" class="tooltipScroll" delayHide={500}><div style={{ maxWidth: '50vw', maxHeight: 300, overflowY: 'scroll' }}>Specifies whether or not the gateway will use the default content security policy.<br/><br/>The default policy inserts the following HTTP headers into all responses:<br/><br/><table style={{textAlign:'left', border:'1px solid black'}}><tbody><tr style={{textAlign:'left', border:'1px solid black'}}><th style={{borderRight:'1px solid black', padding: '10px'}}>header </th><th style={{borderRight:'1px solid black', padding: '10px'}}> value</th></tr><tr><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}>x-frame-options </td><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}> DENY</td></tr><tr><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}>x-xss-protection </td><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}> 1</td></tr><tr><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}>content-security-policy </td><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}> default-src 'self'; frame-ancestors 'self'; form-action 'self';</td></tr><tr><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}>x-content-type-options </td><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}> nosniff</td></tr></tbody></table><br/><br/>Example:<br/><br/>server:<br/>  content_security_policy: disabled<br/></div></ReactTooltip>
