          <ReactTooltip id={"logging_request_log_format_tooltip_id" + idPrefix} place="top" effect="solid" ><div style={{ maxWidth: 300 }}>The format of the events written to the request log. See the Log Format table above for the available directives.<br/></div></ReactTooltip>
