                <div className= {"iag_form_elem iag_tab_heading identity_div" + idPrefix}>
                  <span className="iag_breadcrumb">Identity : </span><br/>                  <h2>CI OIDC (deprecated) Settings</h2><hr/>
                  <Collapsible tabIndex="0" trigger="The configuration entries in this section allow the reverse proxy to act as an OIDC relying party to IBM Security Verify">
                    <p>The configuration entries in this section allow the reverse proxy to act as an OIDC relying party to IBM Security Verify.<br/><br/><h4> Notice of Deprecation</h4><br/><br/>This identity module is no longer preferred and will be removed in a future version. Use the OIDC identity module.<br/><br/>When using the OIDC identity module, `discovery_endpoint` is given rather than `hostname`.<br/><br/><br/>The format for an IBM Security Verify `discovery_endpoint` is:<br/><br/>`https://&lt;hostname&gt;/oidc/endpoint/default/.well-known/openid-configuration`<br/><br/>For example, refer to the IBM Security Verify OIDC configuration below:<br/><br/><span>YAML Example</span><br/><div style={{outline:'2px solid', backgroundColor:'#dcd7d7', padding:'10px', whiteSpace:'pre-wrap'}}><br/>  identity:<br/>    ci_oidc:<br/>      hostname: www.test.com<br/>      client_id: 11111111-2222-3333-4444-5a5a5a5a5a5a5a<br/>      <h2> ...</h2><br/></div><br/><br/>The equivalent OIDC configuration is:<br/><br/><span>YAML Example</span><br/><div style={{outline:'2px solid', backgroundColor:'#dcd7d7', padding:'10px', whiteSpace:'pre-wrap'}}><br/>  identity:<br/>    oidc:<br/>      discovery_endpoint: https://www.test.com/oidc/endpoint/default/.well-known/openid-configuration<br/>      client_id: 11111111-2222-3333-4444-5a5a5a5a5a5a5a<br/>      <h2> ...</h2><br/></div><br/><br/><h4> Response Type Values</h4><br/><table style={{textAlign:'left', border:'1px solid black'}}><tbody><tr style={{textAlign:'left', border:'1px solid black'}}><th style={{borderRight:'1px solid black', padding: '10px'}}>Name </th><th style={{borderRight:'1px solid black', padding: '10px'}}> Description</th></tr><tr><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}>code </td><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}> The authorization code flow will be used to retrieve both an access token and identity token.</td></tr><tr><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}>id_token </td><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}> The implicit flow will be used to retrieve the identity token.</td></tr><tr><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}>id_token token </td><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}> The implicit flow will be used to retrieve both an access token and identity token.</td></tr></tbody></table><br/><h4> Bearer Token Attrs Format</h4><br/>A JSON data element from the bearer token response which should be included or excluded in the credential as an extended attribute.<br/>The format of the configuration entry is:<br/><br/><table style={{textAlign:'left', border:'1px solid black'}}><tbody><tr style={{textAlign:'left', border:'1px solid black'}}><th style={{borderRight:'1px solid black', padding: '10px'}}>`[+</th><th style={{borderRight:'1px solid black', padding: '10px'}}>-]&lt;json-data&gt;`</th></tr></tbody></table><br/>where:<br/><br/><table style={{textAlign:'left', border:'1px solid black'}}><tbody><tr style={{textAlign:'left', border:'1px solid black'}}><th style={{borderRight:'1px solid black', padding: '10px'}}>Element </th><th style={{borderRight:'1px solid black', padding: '10px'}}> Description</th></tr><tr><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}>+ </td><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}> Indicates that this JSON data should be added to the credential.</td></tr><tr><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}>- </td><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}> Indicates that this JSON data should not be added to the credential.</td></tr><tr><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}>&lt;json-data&gt; </td><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}> The corresponding JSON data name, which can also contain pattern matching characters (i.e. * ?).</td></tr></tbody></table><br/>For example the value "-access_token" indicates that the access_token should not be added to the credential.<br/><br/>When a bearer token is received each JSON data element will be evaluated against each rule in sequence until a match is found.<br/>The corresponding code (+|-) will then be used to determine whether the JSON data will be added to the credential or not.<br/>If the JSON data name does not match a configured rule it will by default be added to the credential.<br/><br/><h4> ID Token Attrs Format</h4><br/>A claim from the ID token response which should be included or excluded in the credential as an extended attribute.<br/>The format of the configuration entry is:<br/><br/><table style={{textAlign:'left', border:'1px solid black'}}><tbody><tr style={{textAlign:'left', border:'1px solid black'}}><th style={{borderRight:'1px solid black', padding: '10px'}}>`[+</th><th style={{borderRight:'1px solid black', padding: '10px'}}>-]&lt;claim&gt;`</th></tr></tbody></table><br/>where:<br/><br/><table style={{textAlign:'left', border:'1px solid black'}}><tbody><tr style={{textAlign:'left', border:'1px solid black'}}><th style={{borderRight:'1px solid black', padding: '10px'}}>Element </th><th style={{borderRight:'1px solid black', padding: '10px'}}> Description</th></tr><tr><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}>+ </td><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}> Indicates that this claim should be added to the credential.</td></tr><tr><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}>- </td><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}> Indicates that this claim should not be added to the credential.</td></tr><tr><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}>&lt;claim&gt; </td><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}> The corresponding claim name, which can also contain pattern matching characters (i.e. * ?).</td></tr></tbody></table><br/>For example the value "-email" indicates that the email claim should not be added to the credential.<br/><br/>When an ID token is received each claim will be evaluated against each rule in sequence until a match is found.<br/>The corresponding code (+|-) will then be used to determine whether the claim will be added to the credential or not.<br/>If the claim name does not match a configured rule it will by default be added to the credential.</p>
                  </Collapsible>

                </div>
