          <ReactTooltip id={"identity_ci_oidc_redirect_uri_host_tooltip_id" + idPrefix} place="top" effect="solid" ><div style={{ maxWidth: 300 }}>This is the host which is used in the redirect URI registered with the OIDC OP. If no redirect URI host is configured the host header from the request will be used. The format of the redirect URI will be: `http[s]://&lt;host&gt;/pkmsoidc`.<br/></div></ReactTooltip>
